@use "../../utils/colors";
.userListContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 50px;
}

.userList {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.userList__button {
  background: colors.$primary;
  border: none;
  border-radius: 50px;
  width: 120px;
  height: 34px;
  font-size: 16px;
  margin-top: 50px;
}
